@import "src/components/styles/Landing.module";

.services{
  @extend .landing;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding-bottom: 5%;
  padding-top: 5%;

  @media all and (max-width: 990px) {
    align-items: center;
    padding-bottom: 10%;
    padding-top: 10%;

    p{
      text-align: center;
    }
  }

  .container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    width: 100%;
    margin-top: 5%;

    @media all and (max-width: 990px) {
      grid-template-columns: 1fr;
    }
  }
}